    rightContent,
    longRightContent,
    leftContent,
    eachPostContent,
    leftSideBar,
    addNewClubModal,
    noticeModal,
    messageModal,
    settingModal,
  },
  computed: {
    ...mapState({
      showRightContent: (state) => state.showRightContent,
      showNewClubModal: (state) => state.showNewClubModal,
      showNoticeModal: (state) => state.showNoticeModal,
      showMessageModal: (state) => state.showMessageModal,
      sideBarRadio: (state) => state.sideBarRadio,
      settingModal: (state) => state.settingModal,
    }),
  },
  methods: {
    showRightContentEvent() {
      this.$store.commit('showRightContent');
    },
  },
};
